"use client";

import useSWR from 'swr';
import { BarChart, Bar, XAxis, YAxis, Tooltip, ResponsiveContainer, Cell } from 'recharts';
import { Activity, Eye, TrendingUp } from 'lucide-react';

const fetcher = (url: string) => fetch(url).then(res => res.json());

export default function AnalyticsPage() {
    const { data, error, isLoading } = useSWR('/api/analytics', fetcher);

    if (isLoading) return <div className="p-8 text-secondary">Loading analytics...</div>;
    if (error) return <div className="p-8 text-red-400">Failed to load analytics.</div>;

    const { totalViews, chartData, topPages } = data;

    return (
        <div className="p-8 space-y-8">
            <h1 className="text-3xl font-display font-bold text-white mb-8">Analytics Overview</h1>

            {/* Stats Cards */}
            <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
                <div className="bg-surfaceHighlight p-6 rounded-2xl border border-white/5">
                    <div className="flex items-center gap-4 mb-2">
                        <div className="p-3 bg-accent/20 rounded-xl text-accent">
                            <Eye size={24} />
                        </div>
                        <span className="text-secondary text-sm font-medium uppercase tracking-wider">Total Views</span>
                    </div>
                    <div className="text-4xl font-bold text-white">{totalViews}</div>
                </div>

                <div className="bg-surfaceHighlight p-6 rounded-2xl border border-white/5">
                    <div className="flex items-center gap-4 mb-2">
                        <div className="p-3 bg-green-500/20 rounded-xl text-green-400">
                            <TrendingUp size={24} />
                        </div>
                        <span className="text-secondary text-sm font-medium uppercase tracking-wider">Growth</span>
                    </div>
                    <div className="text-4xl font-bold text-white">+12%</div>
                    <div className="text-xs text-secondary mt-1">vs last week</div>
                </div>

                <div className="bg-surfaceHighlight p-6 rounded-2xl border border-white/5">
                    <div className="flex items-center gap-4 mb-2">
                        <div className="p-3 bg-blue-500/20 rounded-xl text-blue-400">
                            <Activity size={24} />
                        </div>
                        <span className="text-secondary text-sm font-medium uppercase tracking-wider">Active Now</span>
                    </div>
                    <div className="text-4xl font-bold text-white">1</div>
                    <div className="text-xs text-secondary mt-1">Real-time estimate</div>
                </div>
            </div>

            {/* Main Chart */}
            <div className="bg-surfaceHighlight p-6 rounded-2xl border border-white/5 h-[400px]">
                <h3 className="text-xl font-bold text-white mb-6">Views (Last 7 Days)</h3>
                <ResponsiveContainer width="100%" height="100%">
                    <BarChart data={chartData}>
                        <XAxis
                            dataKey="date"
                            stroke="#888888"
                            fontSize={12}
                            tickLine={false}
                            axisLine={false}
                        />
                        <YAxis
                            stroke="#888888"
                            fontSize={12}
                            tickLine={false}
                            axisLine={false}
                            tickFormatter={(value) => `${value}`}
                        />
                        <Tooltip
                            contentStyle={{ backgroundColor: '#1a1a1a', border: '1px solid #333', borderRadius: '8px' }}
                            itemStyle={{ color: '#fff' }}
                            cursor={{ fill: 'rgba(255,255,255,0.05)' }}
                        />
                        <Bar dataKey="views" radius={[4, 4, 0, 0]}>
                            {chartData.map((entry: any, index: number) => (
                                <Cell key={`cell-${index}`} fill="#ff3333" fillOpacity={0.8} />
                            ))}
                        </Bar>
                    </BarChart>
                </ResponsiveContainer>
            </div>

            {/* Top Pages */}
            <div className="bg-surfaceHighlight p-6 rounded-2xl border border-white/5">
                <h3 className="text-xl font-bold text-white mb-6">Top Pages</h3>
                <div className="space-y-4">
                    {topPages.map((page: any, idx: number) => (
                        <div key={idx} className="flex justify-between items-center p-3 hover:bg-white/5 rounded-lg transition-colors">
                            <span className="text-secondary font-mono text-sm">{page.path}</span>
                            <div className="flex items-center gap-2">
                                <span className="text-white font-bold">{page.views}</span>
                                <span className="text-xs text-secondary">views</span>
                            </div>
                        </div>
                    ))}
                    {topPages.length === 0 && <div className="text-secondary">No data yet.</div>}
                </div>
            </div>
        </div>
    );
}
